import React, { useState } from "react";
import { Button, Container, Row, Col, Form, FloatingLabel } from "react-bootstrap";

function LoginScreen(props){
    const [email, setEmail] = useState(null);
    const [password, setPassword] = useState(null);
    return <Container className="d-flex justify-content-center">
        <Form onSubmit={e=>{
            e.preventDefault();
            props.login(email, password);
        }} className="loginForm">
            <Row><Col className="d-flex justify-content-center"><h1 variant="primary">Login</h1></Col></Row>
            <Row>
                <Form.Group>
                    <FloatingLabel label="Email">
                        <Form.Control type="email" placeholder="Email" onChange={e=>setEmail(e.target.value)} />
                    </FloatingLabel>
                </Form.Group>
            </Row>
            <Row className="mt-2">
                <Form.Group>
                    <FloatingLabel label="Passwort">
                        <Form.Control type="password" placeholder="Passwort" onChange={e=>setPassword(e.target.value)} />
                    </FloatingLabel>
                </Form.Group>
            </Row>
            <Row className="mt-3">
                <Col><small>Noch kein Konto? Klicken Sie <a>hier</a>.</small></Col>
                <Col className="col-sm-2 d-flex justify-content-end"><Button variant="primary" type="submit">anmelden</Button></Col>
                
            </Row>
        </Form>
    </Container>;
}

export { LoginScreen };